<template>
  <div class="pager">
    <button class="btn" :class="[current == 1 ? 'btn-pager-disabled' : 'btn-pager']" :disabled="current == 1" @click="prePage">上一页</button>
    <span v-if="pageNo !== 1" class="page-index" :class="1 == current ? 'active':''" @click="goPage(1)">1</span>
    <span v-if="preClipped" class="page-index">...</span>
    <span v-for="index in pages" class="page-index" :class="index == current ? 'active':''" @click="goPage(index)">{{index}}</span>
    <span v-if="backClipped" class="page-index">...</span>
    <span class="page-index" :class="pageNo == current ? 'active':''" @click="goPage(pageNo)">{{pageNo}}</span>
    <button class="btn" :class="[current == pageNo ? 'btn-pager-disabled' : 'btn-pager']" :disabled="current == pageNo" @click="nextPage">下一页</button>
  </div>
</template>

<script>
export default {
  props: {
    // 用于记录总页码，可由父组件传过来
    pageNo: {
      type: Number,
      default: 1
    },
    // 用于记录当前页数，这个与父组件进行数据交互来完成每一页的数据更新，所以我们只要改变current的值来控制整个页面的数据即可
    currentIndex: {
      type: Number,
      default: 1
    }
  },
  data: function () {
    return {
      // 用于判断省略号是否显示
      backClipped: true, 
      preClipped: false,
      current: this.currentIndex
    }
  },
  methods: {
    prePage () {
      // 上一页
      if(this.current > 1){
        let index = this.current - 1
        this.goPage(index)
      }
    },
    nextPage () {
      // 下一页
      if(this.current < this.pageNo){
        let index = this.current + 1
        this.goPage(index)
      }
    },
    goPage (index) {
      // 跳转到相应页面
      if (index !== this.current) {
        this.current = index;
        this.$emit('pagechange', this.current);
      }
    }
  },
  computed: {
    // 使用计算属性来得到每次应该显示的页码
    pages () {
      let ret = []
      if (this.current > 3) {
        // 当前页码大于三时，显示当前页码的前2个
        ret.push(this.current - 2)
        ret.push(this.current - 1)
        if (this.current > 4) {
          // 当前页与第一页差距4以上时显示省略号
          this.preClipped = true
        }
      } else {
        this.preClipped = false
        for (let i = 2; i < this.current; i++) {
          ret.push(i)
        }
      }
      if (this.current !== this.pageNo && this.current !== 1) {
        ret.push(this.current)
      }
      if (this.current < (this.pageNo - 2)) {
        // 显示当前页码的后2个
        ret.push(this.current + 1)
        ret.push(this.current + 2)
        if (this.current <= (this.pageNo - 3)) {
          //当前页与最后一页差距3以上时显示省略号
          this.backClipped = true
        }
      } else {
        this.backClipped = false
        for (let i = (this.current + 1); i < this.pageNo; i++) {
          ret.push(i)
        }
      }
      // 返回整个页码组
      return ret
    }
  }
}
</script>

<style scoped>
.pager {
  text-align: center;
  display: inline-block;
}
.btn-pager, .btn-pager-disabled {
  margin: 0 3px;
  padding: 0 10px;
  width: auto;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background-color: #ededed;
  color: #000000;
  border: 0;
  border-radius: 0px;
  vertical-align: middle;
}
.page-index {
  display: inline-block;
  margin: 0 3px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  background-color: #ededed;
  cursor: pointer;
  color: #000000;
}
.btn-pager-disabled {
  background-color: #ededed;
  cursor: not-allowed;
  color: #bfcbd9;
}
.active, .page-index:hover, .btn-pager:hover {
  color: #ffffff;
  background-color: #ff5256;
}
</style>


